export default {
  props: {
    load: Number,
  },
  data() {
    return {
      perPage: 5,
      currentPage: 1,
      fields: [
        {
          key: 'name1',
          label: 'คนท้า',
        },
        {
          key: 'name2',
          label: 'คนได้รับคำท้า',
        },
        {
          key: 'topic',
          label: 'หัวข้อที่ทำการท้า',
        },
        {
          key: 'coin',
          label: 'มูลค่า',
          sortable: true,
        },
        {
          key:'winner',
          label:'ผู้ชนะ'
        }
      ],
      items: [],
      st: 0,
    }
  },
  async mounted() {
    await this.$store.dispatch('LoadBetResult')
    this.items = this.$store.getters.getBetResult
  },
  computed: {
    rows() {
      return this.items.length
    },
    test() {
      return this.$store.getters.getBetSend
    },
  },
  methods: {
    nextPage() {
      if (this.currentPage * this.perPage < this.items.length)
        this.currentPage++
    },
    prePage() {
      if (this.currentPage > 1) this.currentPage--
    },
  },
  beforeDestroy() {
    this.$store.commit('setBetResult', '')
  },
}
